import { Component } from '@angular/core';
import { AlertController, IonicPage, ModalController, NavController, ViewController } from 'ionic-angular';
import { User } from '../../models/user';
import { AppConfigProvider } from '../../providers/appconfig/appconfig';
import { ContentProvider } from '../../providers/content/content';
import { Storage } from '@ionic/storage';

@IonicPage()
@Component({
  selector: 'page-settings',
  templateUrl: 'settings.html',
})
export class SettingsPage {

  medarbeider: boolean;
  user = {} as User;
  BoJoKo: boolean;
  datetimeFirst: boolean;
  hideCountdown: any;

  constructor(
    private navCtrl: NavController,
    private viewCtrl: ViewController,
    private acp: AppConfigProvider,
    private contentProvider: ContentProvider,
    private storage: Storage,
    private modalCtr: ModalController,
    private cntProvider: ContentProvider,
    private alertCtrl: AlertController
  ) {
    this.user = this.acp.getUser();
    this.BoJoKo = acp.isSubscribedToTrack('BoJoKo');
    this.storage.get('datetimeFirst').then(value => {
      this.datetimeFirst = value;
    });
    this.storage.get('hideCountdown').then(value => {
      this.hideCountdown = value;
    });
    this.storage.get('hideCountdown').then(value => {
      this.hideCountdown = value;
    });
  }

  close(): void {
    this.viewCtrl.dismiss();
  }

  login() {
    this.navCtrl.push('LoginPage');
  }

  changeArrangement() {
    this.navCtrl.pop().then(() => {
      this.navCtrl.push('HomePage');
    });
  }

  showTutorial() {
    this.navCtrl.pop().then(() => {
      this.navCtrl.push('TutorialPage');
    });
  }

  showAboutThisApp() {
    this.navCtrl.push('AboutPage');
  }

  togggleChildren() {
    if (this.BoJoKo === true) {
      this.acp.addTrack('BoJoKo');
    } else {
      this.acp.removeTrack('BoJoKo');
    }
  }

  toggleDateBeforeInfo() {
    if (this.datetimeFirst === true) this.storage.set('datetimeFirst', true); else {
      this.storage.set('datetimeFirst', false);
    }
  }

  toggleHideCountdown() {
    if (this.hideCountdown === true) this.storage.set('hideCountdown', true); else {
      this.storage.set('hideCountdown', false);
    }
  }

  toggleMedarbeider() {
    const alert = this.alertCtrl.create({
      title: 'Medarbeiderinfo',
      message: 'For å sla på medarbeiderinfo må du vite passordet.',
      inputs: [
        {
          name: 'passord',
          placeholder: 'passord',
          type: 'password',
        }
      ],
      buttons: [
        {
          text: 'Avbryt',
          handler: () => {
            this.medarbeider = false;
            return;
          }
        },
        {
          text: 'Logg inn',
          handler: (data) => {
            if (data.passord === 'Medarbeider2018') {
              this.enableMedarbeider();
              this.showAlert('Suksess', 'Medarbeiderinfo er nå slått på.');
            } else {
              this.medarbeider = false;
              this.showAlert('Feil passord', '');
            }
            return;
          }
        }
      ]
    });
    alert.present();
  }

  enableMedarbeider() {
    if (this.medarbeider === true) {
      this.storage.set('medarbeider', true);
      this.acp.addTrack('TenOaseMedarbeider');
    } else {
      this.storage.set('medarbeider', false);
      this.acp.removeTrack('TenOaseMedarbeider');
    }
  }

  newPerson() {
    this.acp.getArrangement().then(arrangement => {
      const person = {
        arrangement: `${arrangement}`,
      };
      const modal = this.modalCtr.create('PersonCreatorPage', { person });
      modal.onDidDismiss(freshData => {
        if (freshData) {
          this.cntProvider.postPerson(freshData);
        }
      });
      modal.present();
    });
  }

  BatchEdit() {
    this.navCtrl.push('BatchEditPage').then(() => this.showAlert('Done', 'Finished edit/importing events'));
  }

  private showAlert(title, message) {
    const alert = this.alertCtrl.create({
      title: title,
      message: message,
      buttons: [
        {
          text: 'Ok',
          handler: () => {
            return;
          }
        }
      ]
    });
    // noinspection JSIgnoredPromiseFromCall
    alert.present();
  }

}



